<template>
  <div id="main-container">

    <div class="slide__container">
      <div
        v-for="(item, index) in titleList" :key="index"
        class="wrapper">
        <div
          @click="_showHint(item)"
          :class="[item.titleList.titleRate.code]">
          <div class="dinner__card__level">
            {{ item.titleList.titleRate.value }}
          </div>
          <div
            class="book__mark">
            {{ index + 1 }}
          </div>
          <div
            v-if="item.unLocked && !item.checked"
            class="dinner__card__dinner__name">
            획득가능!
          </div>
          <div
            v-if="item.unLocked && item.checked"
            class="dinner__card__dinner__name">
            {{ item.titleList.titleName }}
          </div>
          <div
            v-if="!item.unLocked && !item.checked"
            class="dinner__card__dinner__name">
            ???
          </div>
          <!--아래의 경우는 있을 수 없는 경우이나 예외를 위해 추가-->
          <div
            v-if="!item.unLocked && item.checked"
            class="dinner__card__dinner__name">
            ???
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'pinia'
import { usersStore } from '@/store/users'
import sweetAlert from '@/wrapper/sweet-alert'
import jwtDecode from 'jwt-decode'

export default {
  name: 'user-title-list',
  data () {
    return {
      rate: 'NORMAL',
      titleList: [
        {
          userTitleOid: 0,
          unLocked: false,
          checked: false,
          titleList: {
            titleRate: { code: '', value: '' },
            titleName: '',
            titleHint: '',
            titleCondition: '',
            orderSeq: 0
          }
        }
      ]
    }
  },
  computed: {
    ...mapState(usersStore, ['userCustomInfo', 'loggedInAccessToken'])
  },
  methods: {
    _showHint (item) {
      if (item.unLocked && !item.checked) {
        sweetAlert.question(null, '새로운 업적을 획득합니다!', '획득', '나중에').then(con => {
          if (con.value) {
            this.unLockTitle(this.userCustomInfo.userOid, item.userTitleOid).then(res => {
              this.titleList.find(it => it.userTitleOid === res.userTitleOid).checked = true
              sweetAlert.noIcon(`${res.titleList.titleName} 업적을 획득했습니다!`, '확인')
            })
          }
        })
      } else if (item.unLocked && item.checked) {
        sweetAlert.noIcon(item.titleList.titleCondition, '확인')
      } else if (!item.unLocked && !item.checked) {
        sweetAlert.noIcon(item.titleList.titleHint, '확인')
      } else {
        return null
      }
    }
  },
  created () {
    if (this.userCustomInfo === undefined) {
      sweetAlert.noIcon('잘못된 경로로 진입하셨거나 인증 정보가 없습니다. 로그인 화면으로 이동합니다.', '확인')
      this.logout()
    } else {
      if (new Date(jwtDecode(this.loggedInAccessToken).exp * 1000) <= new Date()) {
        sweetAlert.noIcon('자동 로그인 기간이 만료 되었습니다. 다시 로그인 해 주세요 :)', '확인')
        this.logout()
      }
    }
  },
  mounted () {
    this.getTitleList(this.userCustomInfo.userOid).then(res => {
      this.titleList = res
    })
  }
}
</script>
<style scoped lang="scss">
.NORMAL {
  position: relative;
  width: 250px;
  display: inline-block;
  margin: auto;
  border-radius: 20px;
  text-align: center;
  box-shadow: -1px 15px 30px -12px black;
  background: #FFFFFF;
  background-size: 400% 400%;

}
.RARE {
  position: relative;
  width: 250px;
  display: inline-block;
  margin: auto;
  border-radius: 20px;
  text-align: center;
  box-shadow: -1px 15px 30px -12px black;

  background: linear-gradient(-45deg, #D5FFE4, #8BE8E5, #A084E8, #6F61C0);
  background-size: 400% 400%;
  animation: gradient 5s ease infinite;

  @keyframes gradient {
    0% {
      background-position: 0 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0 50%;
    }
  }
}
.ULTIMATE {
  position: relative;
  width: 250px;
  display: inline-block;
  margin: auto;
  border-radius: 20px;
  text-align: center;
  box-shadow: -1px 15px 30px -12px black;

    background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
    background-size: 400% 400%;
    animation: gradient 1.5s ease infinite;

  @keyframes gradient {
    0% {
      background-position: 0 50%;
    }
    50% {
      background-position: 100% 50%;
    }
    100% {
      background-position: 0 50%;
    }
  }
}
//$NORMAL: #4FACFF;
//$RARE: #EE5487;
//$ULTIMATE: #A084DC;
</style>
